<template>
  <div class="calendar">
    <h2>{{ name }}</h2>
    <div class="row">
      <span
        class="head"
        v-for="(item, index) in showWeek"
        :key="`${index}${item}`"
        >{{ item }}</span
      >
    </div>
    <div class="row" v-for="(item, index) in daysArray" :key="`r-${index}`">
      <span
        :class="`item ${
          item !== 0 && checked.indexOf(item) > -1 ? 'checked' : ''
        }`"
        v-for="(item, index) in item"
        :key="`${index}${item}`"
        >{{ item === 0 ? "" : item }}</span
      >
    </div>
    <footer>
      <small>{{ year }} / {{ monthC }}</small>
    </footer>
  </div>
</template>
<script>
export default {
  props: ["month", "checked", "name"],
  data() {
    return {
      week: ["S", "M", "T", "W", "T", "F", "S"],
      days: [0, 31, 28, 31, 30, 31, 30, 31, 31, 30, 31, 30, 31],
      start_param: 0, // 是否从周日开始
      showWeek: [],
    };
  },
  computed: {
    year() {
      const _date = new Date(this.month || null);
      return Number(_date.getFullYear());
    },
    monthC() {
      const _date = new Date(this.month || null);
      return Number(_date.getMonth() + 1);
    },
    daysArray() {
      const _date = new Date(this.month || null),
        _year = Number(_date.getFullYear()),
        _month = Number(_date.getMonth() + 1);
      _date.setDate(1);
      const _days = this.days[_month];
      const first_day = _date.getDay() || 7,
        label_html = "",
        showWeek = [];
      let start_type = this.start_param ? 0 : 1; // 周几开始
      for (let i = start_type; i <= 6 + start_type; i++) {
        let _label = i === 7 ? this.week[0] : this.week[i];
        showWeek.push(_label);
        this.showWeek = showWeek;
      }
      let cur_l = 0,
        cur_list = [],
        _list = [];
      for (let i = 1; i <= _days; i++) {
        if (i == 1) {
          cur_l = (first_day + this.start_param + 6) % 7;
          for (let j = 0; j < cur_l; j++) cur_list.push(0);
        }
        if (cur_l === 7) {
          _list.push(cur_list);
          (cur_list = []), (cur_l = 0);
        }
        if (i === _days) {
          _list.push(cur_list);
        }
        cur_list.push(i);
        cur_l++;
      }
      return _list;
    },
  },
  mouted() {
    if (this.year % 4 === 0 && this.year % 100 != 0) {
      // 闰年
      this.days[2] = 29;
    }
  },
};
</script>
<style scoped lang="less">
