  background-color: #d6cbee;
  border-radius: 10px;
  padding: 10px 25px;
  padding-left: 20px;
  text-align: center;
  position: relative;
}
.row {
  display: flex;
  text-align: center;
  font-size: 16px;
  font-weight: bold;
  margin-bottom: 5px;
  span {
    display: inline-block;
    width: 30px;
    height: 30px;
    line-height: 30px;
    border-radius: 50%;
    margin-left: 5px;
    &.checked {
      background-color: #b5a9cf;
    }
  }
}
footer {
  position: absolute;
  top: 10px;
  right: 25px;
}
</style>
