<template>
  <div class="lib-container">
    <div class="left">
      <h1>标签贴生成器</h1>
      <div class="form">
        <el-upload
          class="upload-demo"
          drag
          action="https://jsonplaceholder.typicode.com/posts/"
          :on-success="handleChange"
          :on-progress="
            () => {
              loading = true;
            }
          "
        >
          <i class="el-icon-upload"></i>
          <div class="el-upload__text">将文件拖到此处，或<em>点击上传</em></div>
          <div class="el-upload__tip" slot="tip">
            只能上传csv文件，且不超过500kb
          </div>
        </el-upload>
      </div>
      <h2>相关设置</h2>
      <el-form ref="form" :model="form" label-width="80px">
        <el-form-item label="相关月">
          <el-date-picker type="month" placeholder="选择月" v-model="form.cardMonth">
          </el-date-picker>
        </el-form-item>
      </el-form>
    </div>
    <div class="right">
      <h1>标签预览</h1>
      <div class="calendar" id="assessContent">
        <Calendar :month="form.cardMonth" :checked="checkList" name="学习" />
      </div>
      <el-button
        type="primary"
        round
        icon="el-icon-download"
        :loading="loading"
        @click="exportPic('assessContent')"
        >生成卡片图片</el-button
      >
    </div>
  </div>
</template>
<script>
import { read, utils } from "xlsx"; // 注意处理方法引入方式
import html2Canvas from "html2canvas";
import Calendar from "../components/Calendar.vue";
export default {
  components: { Calendar },
  data() {
    return {
      fileList: [],
      tableData: [],
      tableHead: [],
      checkList: [0],
      loading: false,
      form: {
      cardMonth: null,
      },
    };
  },
  methods: {
    // 导入成功时执行
    handleChange(res, file, fileList) {
      this.loading = false;
      // 将文件放入
      for (let i = 0; i < fileList.length; i++) {
        if (file.name != fileList[i].name) {
          this.fileList.push({
            name: file.name,
            url: "",
            uid: file.uid,
          });
        }
      }
      const files = { 0: file };
      this.readExcel(files);
    },
    readExcel(file) {
      const fileReader = new FileReader();

      fileReader.onload = (ev) => {
        try {
          const data = ev.target.result;
          const workbook = read(data, { type: "binary" });
          const params = [];
          // 取对应表生成json表格内容
          workbook.SheetNames.forEach((item) => {
            this.tableData.push(utils.sheet_to_json(workbook.Sheets[item]));
          });
          console.log(this.tableData);
          // 该算法仅针对表头无合并的情况
          if (this.tableData.length > 0) {
            // 获取excel中第一个表格数据tableData[0][0]，并且将表头提取出来
            for (const key in this.tableData[0][0]) {
              this.tableHead.push(key);
            }
          }
          //   this.loading = false;
          this.getTracker("学习", this.tableData[0]);
          // 重写数据
        } catch (e) {
          console.log("error:" + e);
          //   this.loading = false;
          return false;
        }
      };
      fileReader.readAsBinaryString(file[0].raw);
    },
    getTracker(name, list) {
      let c_list = [0];
      list.sort(sortBy("Day", 1)); //attr：根据该属性排序；rev：升序1或降序-1，不填则默认为1
      list.map((item) => {
        if (item[name] === "Yes") c_list.push(item["Day"]);
      });
      this.checkList = c_list;
      function sortBy(attr, rev) {
        if (rev == undefined) {
          rev = 1;
        } else {
          rev ? 1 : -1;
        }
        return function (a, b) {
          a = a[attr];
          b = b[attr];
          if (a < b) {
            return rev * -1;
          }
          if (a > b) {
            return rev * 1;
          }
          return 0;
        };
      }
    },
    // 导出图片
    exportPic(DivID) {
      html2Canvas(document.querySelector("#" + DivID), {
        backgroundColor: null,
      }).then((canvas) => {
        let dataURL = canvas.toDataURL("image/png");
        if (dataURL !== "") {
          var a = document.createElement("a");
          a.download = "卡片";
          a.href = dataURL;
          a.click();
        }
      });
    },
  },
};
</script>
